html {
    --color-primary: #ea4c6b;
    --color-primary-dark: #bb3c55;
    --color-link: #4c6bea;
    --color-link-visited: #7c4cea;
    --color-link-hover: #3c55bb;
    --color-link-active: #bb3c55;
    --color-black: #fff;
    --color-gray1: #efeff0;
    --color-gray2: #dededf;
    --color-gray3: #cdcdce;
    --color-gray4: #7d7d7e;
    --color-gray5: #5a5a5b;
    --color-gray6: #3c3c3d;
    --color-white: #000;
}

/*
    Dashboard Basic Layout
*/

.dashboard-layout {
    display: grid;
    grid-template-areas:
        "header     header"
        "navigation content";
    grid-template-columns: 10em 1fr;
    grid-template-rows: var(--rhythm-double) 1fr;
}

/* Header styles */

.header {
    /* --rhythm: 1.5rem; */
    grid-area: header;
    padding: var(--rhythm-half);
}

.title {
    --sub-rhythm: 2rem;

    font-size: var(--font-size);
    line-height: var(--line-height);
    margin: 0;
}

/* Navigation Styles */
.navigation {
    background-color: var(--color-gray1);
    grid-area: navigation;
    padding: var(--rhythm) var(--rhythm-half);
}

.navigation ul {
    list-style-type: none;
    margin-left: 0;
}

/* Content Styles */
.content {
    grid-area: content;
    padding: 0 var(--rhythm-half);
}

/*
    Cutestrap Styleguide Specific Items
*/

.hide-gridlines {
    background-image: none !important;
}
